import styles from "./Videography.module.css";
import YoutubeEmbed from "../../components/GallerieVideo/GallerieVideo";
import { Helmet } from "react-helmet-async";

export default function Videography() {
  return (
    <div className={styles.container}>
      <Helmet>
        <title>Videography - Portfolio Riad</title>
        <meta
          name="description"
          content="gallerie de la collection Videographique de riad realisee par drone DJI en tant que photographe amateur"
        />
      </Helmet>
      <div className={styles.titre}>
        <h1>Ma collection photographique</h1>
      </div>
      <YoutubeEmbed embedId="9gZPdIAYT0Y" />
      <YoutubeEmbed embedId="d6bRdduentE" />
      <YoutubeEmbed embedId="k6Jio4V03Yg" />
    </div>
  );
}
