import styles from "./GallerieVideo.module.css";
import PropTypes from "prop-types";

// https://dev.to/bravemaster619/simplest-way-to-embed-a-youtube-video-in-your-react-app-3bk2    code pour intÃ©grqtion de video youtube
//si tu a Adblock activee, il y aura des warning sur la console du navigateur lorsque tu passe a la page videography, il faut juste desactivee Adblock et y aura plus de warnings

const YoutubeEmbed = ({ embedId }) => (
  <div>
    <div className={styles.video_responsive}>
      <iframe
        width="560"
        height="315"
        src={`https://www.youtube.com/embed/${embedId}`}
        frameBorder="0"
        snapshotat={10}
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
        allowFullScreen
        title="Embedded youtube"
      />
    </div>
  </div>
);

YoutubeEmbed.propTypes = {
  embedId: PropTypes.string.isRequired,
};

export default YoutubeEmbed;
